@import "/src/Resources/scss/customs-mixin.scss";
@import "/src/Resources/scss/variables/variables";

#TablePreferences {
    width: 100%;
    filter: drop-shadow(0px 4px 24px rgba(0, 0, 0, 0.06));
    
    .title {
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #5E5873;
    }

    .last-column {
        text-align: end;
    }

    .gruop {
        @include displayRow(10px, baseline, center);
        
        p {
            font-size: 14px;
            line-height: 21px;
            color: #5E5873;
        }
    }

    .actions-wrapper {
        @include displayRow(5px, end, center);

    }

    .key-text {
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
        text-transform: uppercase;
        color: #2C2C26;
    }

    .eidt-icon {
        color: #575975;
    }
}