@import "/src/Resources/scss/customs-mixin.scss";

#AddPreferencesBtn {
    display: flex;
    max-width: 280px;
    max-height: 180px;
    flex-direction: column;
    justify-content: space-evenly;

    .title {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #2C2C26;
        padding-left: 1rem;
    }

    .text {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #5F5974;
        padding-left: 1rem;

    }

    .btn-style {
        @include displayRow(5px, space-between, center);

        width: fit-content;
        .text {
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            text-align: center;
        }
    }
}