#PreferencesPage {
    .nav {
        &.nav-tabs {
            margin-bottom : 0px !important;
        }
    } 

    .nav-link {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        text-align: center;
        border-left: 1px solid #ebe9f1;
        
        &.first-nav {
            border: none;
        }
    }

    .table-tab {
        display: flex;
    }
}